<template>
  <div class="row w-100 mx-0 mt-3 p-0 bfont">
    <div class="col-12">
      <div class="card">
        <div class="row w-100 m-0 p-2">
          <div class="col-12 text-right my-3">
            <button
              type="button"
              class="btn btn-info bfont"
              data-toggle="modal"
              @click="
                $router.push({
                  name: `Position`,
                })
              "
            >
              กลับ
            </button>
          </div>
          <div class="col-12 mt-3 mx-0 p-3">
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1">ชื่อตำเเหน่ง</label>
              <input
                type="text"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.position_name"
              />
            </div>
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1"
                >เวลาเข้างานเต็มวัน {{ add.full_time_in }}</label
              >
              <input
                type="time"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.full_time_in"
              />
            </div>
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1"
                >เวลาเข้าสายเต็มวัน {{ add.full_time_late }}</label
              >
              <input
                type="time"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.full_time_late"
              />
            </div>
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1"
                >เวลาเข้างานครึ่งวัน {{ add.half_time_in }}</label
              >
              <input
                type="time"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.half_time_in"
              />
            </div>
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1"
                >เวลาเข้าสายครึ่งวัน {{ add.half_time_late }}</label
              >
              <input
                type="time"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.half_time_late"
              />
            </div>
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1"
                >เวลาออกงาน {{ add.full_time_out }}</label
              >
              <input
                type="time"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.full_time_out"
              />
            </div>
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1"
                >เวลาตัด F-H {{ add.refer_half_time }}</label
              >
              <input
                type="time"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.refer_half_time"
              />
            </div>
          </div>
          <!-- --------------------- -->
          <div class="col-12 text-right">
            <button type="button" class="btn btn-info" @click="adddata()">
              ยืนยัน
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "EditPosition",
  props: ["id"],
  data() {
    return {
      add: {
        position_name: "",
        full_time_in: "",
        full_time_out: "",
        full_time_late: "",
        half_time_in: "",
        half_time_late: "",
        refer_half_time: "",
      },
    };
  },
  mounted() {
    this.redata();
    this.$store.dispatch("positionActions/edit", { id: this.id });
  },
  watch: {
    getdata(value) {
      // console.log(value);
      this.add = {
        position_name: value.position_name,
        full_time_in: value.full_time_in,
        full_time_out: value.full_time_out,
        full_time_late: value.full_time_late,
        half_time_in: value.half_time_in,
        half_time_late: value.half_time_late,
        refer_half_time: value.refer_half_time,
      };
    },
  },
  computed: {
    getdata() {
      return this.$store.getters["positionActions/GET_edit"];
    },
  },

  methods: {
    redata() {
      this.add = {
        position_name: "",
        full_time_in: "",
        full_time_out: "",
        full_time_late: "",
        half_time_in: "",
        half_time_late: "",
        refer_half_time: "",
      };
    },
    async adddata() {
      try {
        var date = this.$dt(Date.now(), "yyyy-mm-dd");
        if (
          this.add.position_name == "" ||
          this.add.full_time_in == "" ||
          this.add.full_time_out == "" ||
          this.add.full_time_late == "" ||
          this.add.half_time_in == "" ||
          this.add.half_time_late == "" ||
          this.add.refer_half_time == ""
        ) {
          throw "กรอกข้อมูลไม่ครบ";
        } else if (
          new Date(`${date}T${this.add.full_time_in}`) >
            new Date(`${date}T${this.add.full_time_out}`) ||
          new Date(`${date}T${this.add.full_time_late}`) >
            new Date(`${date}T${this.add.full_time_out}`) ||
          new Date(`${date}T${this.add.half_time_in}`) >
            new Date(`${date}T${this.add.full_time_out}`) ||
          new Date(`${date}T${this.add.half_time_late}`) >
            new Date(`${date}T${this.add.full_time_out}`) ||
          new Date(`${date}T${this.add.refer_half_time}`) >
            new Date(`${date}T${this.add.full_time_out}`) ||
          new Date(`${date}T${this.add.full_time_in}`) >
            new Date(`${date}T${this.add.full_time_late}`) ||
          new Date(`${date}T${this.add.half_time_in}`) >
            new Date(`${date}T${this.add.half_time_late}`)
        ) {
          throw "ระบุช่วงเวลาไม่ถูกต้อง";
        } else {
          let data = {
            id: this.id,
            position_name: this.add.position_name,
            full_time_in: this.add.full_time_in,
            full_time_out: this.add.full_time_out,
            full_time_late: this.add.full_time_late,
            half_time_in: this.add.half_time_in,
            half_time_late: this.add.half_time_late,
            refer_half_time: this.add.refer_half_time,
          };
          await this.$store.dispatch("positionActions/addedit", data);
          this.$router.push({
            name: `Position`
          });
          this.$main.showSuccessAlert(this, "เรียบร้อย");
        }
      } catch (error) {
        alert(error);
      }
    },
  },
};
</script>

